/* Position and sizing of burger button */
.bm-burger-button {
    position: absolute;
    width: 2rem;
    height: 2rem;
    margin-top: 7px;
    margin-left: 2rem;
    z-index: 8 !important;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: white;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: whitesmoke;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: whitesmoke;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #3490dc;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #3490dc;
    width: 41% !important;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
 /* Individual item */
.bm-item {
    display: inline-block;
    color: whitesmoke;
    margin-bottom: 10px;
    text-align: left;
    text-decoration: none;
    transition: color 0.2s;
    font-size: 1.25rem;
  }
  
  .bm-item:hover {
    color: #ffffff;
  }
  .bm-item:focus {
    outline:none;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.5) !important;
  }

  /*Custom CSS for Ticket Page Sidebar */
  #ticketPage #push1[style*=transition] #title{
    margin-left: -8% !important;
  }

  #ticketPage #react-burger-menu-btn{
    background: url('../images/arrow.png') !important;
    transform: scale(.8);
    width: 50px !important;
    height: 50px !important;
  }

  #ticketPage .bm-menu-wrap{
    position: absolute !important;
    z-index: 8 !important;
  }

  #ticketPage #push1[style*=transition]{
    margin: auto 12%;
  }

  #ticketPage .bm-cross-button{
    background: url('../images/arrow.png') !important;
    transform: scale(.8) rotate(180deg);
    width: 50px !important;
    height: 50px !important;
  }

  #ticketPage .bm-overlay{
    display: none;
  }

  #ticketPage .bm-cross-button span{
    display: none;
  }

  body[style*="overflow-x"] .bm-burger-button{
    display: none;
  }

  #ticketPage .bm-item-list{
    padding: 0 0 0;
  }

  #ticketPage .bm-menu{
    background-color: whitesmoke;
    padding: 0 0 0;
    text-align: center;
    border-right: 2px groove;
    border-color: lightgray;
  }

  #ticketPage .bm-menu label, #ticketPage .bm-item, #ticketPage .bm-item:hover{
    color: black;
    font-size: small;
  }

  #ticketPage .bm-burger-button{
    margin-left: 1.5rem;
    z-index: unset !important;
  }

  #ticketPage .bm-burger-button span{
    display: none;
  }

  #ticketPage .bm-overlay{
    opacity: 0 !important;
    z-index: 7 !important;
  }

  /* Smaller devices like tablets */
  @media(max-width:800px){

    #ticketPage #push1[style*=transition] #title{
      margin: auto auto auto -2% !important;
    }

    #ticketPage .card-body,  #ticketPage .card-header{
      padding: .3em;
    }

    #ticketPage .card-body > p{
      margin-top: 1px;
      margin-bottom: 1px;
    }

    #ctcTitle{
      margin-left: 7%;
    }

    #ticketPage #react-burger-menu-btn{
      background: url('../images/arrow.png') !important;
      width: 50px !important;
      height: 50px !important;
      transform: scale(.6);
      top: -7px !important;
      right: -7px !important;
    }

    #ticketPage .bm-cross-button{
      background: url('../images/arrow.png') !important;
      transform: scale(.6) rotate(180deg);
      width: 50px !important;
      height: 50px !important;
      top: -7px !important;
      right: -7px !important;
    }

}