/* Custom color pallete
    Defined in: 
        - App.css
        - logStyles.css
*/
:root {
    --customBlue: #3490DC;
    --customLightBlue: #61dafb;
}

p {
    margin-top: 1rem;
}

input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 1px solid #B0C4De;
    border-radius: 15px;
    background-color: #F8F8FF;
}

.createTitle {
    width: 100%;
}

.createBody {
    width: 100%;
    height: 400px;
}

.addPhoto {
    width: auto;
    border: none;
    padding: 0;
    background-color: white;
    float: left;
}

.imgPreview {
    float: left;
    width: 150px;
    height: 150px;
    margin-right: 10px;
    margin-bottom: 10px;
    border-color: rgb(146, 146, 146);
    border-style: dashed;
}

select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 1px solid #B0C4De;
    border-radius: 5px;
    background-color: #F8F8FF;
}

select:focus {
    outline: none;
}

input:focus {
    outline: none;
}

/* FOR VERSION ROLLOUTS */
#rolloutButton {
    background-color: var(--customBlue);
    min-width: 95px;
}

/* LOADING GIF */
#loadingDiv svg {
    animation: rotation infinite 4s linear;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/* SIDE PANELS */
.notifySide {
    background-color: "whitesmoke";
    position: absolute;
    right: 0;
    overflow-y: scroll;
    overflow-wrap: break-word;
    width: 250px;
    height: 93vh;
    margin-top: 0px;
    float: right;
    border-left: 3px groove;
    z-index: 10;
    box-shadow: 1px 12px 8px 8px #454545;
    padding: 1rem;
    border-radius: 5%;
    text-align: center;
}

.react-search-field, .react-search-field-button {
    float: right;
    margin-right: 10%;
    border: none !important;
}

.react-search-field-button {
    display: none;
}

.card {
    box-shadow: 1px 6px 9px 0px #454545;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.1);
}

.accordion>.card:hover {
    transform: scale(1);
}

.btn {
    box-shadow: inset -1px -4px 11px -5px #454545;
}

tbody {
    background-color: whitesmoke;
}

.mainFocus {
    z-index: 9;
    width: 100vw;
    height: 100vh;
    background-color: black;
    opacity: .5;
    position: fixed;
    top: 0;
    left: 0;
}

/* Custom Logout CSS*/
.navbar-brand:hover {
    color: white;
}

/* Toggle Switch CSS */
.wrg-toggle {
    touch-action: pan-x;
    display: inline-block;
    position: relative;
    cursor: pointer;
    background-color: transparent;
    border: 0;
    padding: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
}

.wrg-toggle-input {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.wrg-toggle-check, .wrg-toggle-uncheck {
    position: absolute;
    width: 10px;
    height: 10px;
    top: 0;
    bottom: 0;
    margin-top: auto;
    margin-bottom: auto;
    line-height: 0;
    opacity: 0;
    transition: opacity .25s ease;
}

.wrg-toggle-check {
    left: 8px;
}

.wrg-toggle-uncheck {
    opacity: 1;
    right: 10px;
}

.wrg-toggle-uncheck span, .wrg-toggle-check span {
    align-items: center;
    display: flex;
    height: 10px;
    justify-content: center;
    position: relative;
    width: 10px;
}

.wrg-toggle-container {
    width: 50px;
    height: 24px;
    padding: 0;
    border-radius: 30px;
    background-color: #4d4d4d;
    transition: all .2s ease;
}

.wrg-toggle-circle {
    transition: all .5s cubic-bezier(.23, 1, .32, 1) 0ms;
    position: absolute;
    top: 1px;
    left: 1px;
    width: 22px;
    height: 22px;
    border: 1px solid #4d4d4d;
    border-radius: 50%;
    background-color: #fafafa;
    box-sizing: border-box;
    transition: all .25s ease;
}

.wrg-toggle--checked .wrg-toggle-check {
    opacity: 1;
}

.wrg-toggle--checked .wrg-toggle-uncheck {
    opacity: 0;
}

.wrg-toggle--checked .wrg-toggle-circle {
    left: 27px;
}

.wrg-toggle--disabled {
    cursor: not-allowed;
    opacity: 0.5;
    -webkit-transition: opacity 0.25s;
    transition: opacity 0.25s;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}